@import url('https://fonts.googleapis.com/css2?family=Comfortaa:wght@300&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Comfortaa', cursive;
  color: rgb(97, 218, 251);
}
body{
  background-color: #282c34;
}
.App {
  text-align: center;
}
.Links{
  margin: 0 auto;
  max-width: 1000px;
  display: flex;
  flex-direction: column;
}
.Link{
  display: grid;
  grid-template-columns: 100px 1fr 200px;
  border-bottom: 1px solid rgb(97, 218, 251);
  word-break: break-word;
}
.Link:last-child{
  border-bottom: none;
}
.Link button{
  width: 100%;
  color: black;
  padding: 20px 20px;
  /* height: 40px; */
  /* h100% */
  cursor: pointer;
}
.Link a{
  color: black;
  text-decoration: none;
  cursor:auto;
}
.Link img{
  width: 200px;
  cursor: pointer;
}
.Link img:hover{
  /* opacity: 0.9; */
}
.Link h2{
  font-size: 18px;
  font-weight: normal;
}
.Link pre{
  font-size: 14px;
  text-align: left;
  padding: 10px 0;
}
.desc{
  padding: 0 10px;
  min-width: 200px;
}
ul{
  text-align: left;
  padding: 20px;
}
li{
  text-align: left;
}


.myModal {
  z-index: 5;
  position: fixed;
  top: 0;
  left: 0;
  display: none;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.637);
  margin: 0 auto;
}
.myModalContent {
  /* width: 100vw; */
  padding: 15px;
  background: rgba(78, 92, 119, 0.907);
  border-radius: 5px;
}
.myModalContent img{
  width: 100%;
  height: 100%;
}
.myModal.active {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}